// File: component/Chat/StyleSelector.tsx
interface StyleSelectorProps {
  selectedStyle: string;
  onStyleChange: (style: string) => void;
}

const StyleSelector: React.FC<StyleSelectorProps> = ({ selectedStyle, onStyleChange }) => {
  return (
    <div className="flex items-center space-x-2 text-sm text-gray-300">
      <select
        value={selectedStyle}
        onChange={(e) => onStyleChange(e.target.value)}
        className="p-1 pr-8 border border-gray-700 rounded-md bg-gray-800 focus:outline-none focus:ring-2 focus:ring-gray-600"
      >
        <option value="normal">Choose style</option>
        <option value="professional">Professional</option>
        <option value="casual">Casual</option>
      </select>
    </div>
  );
};
