// File: component/Chat/ChatInput.tsx
import React, { useRef, useState } from 'react';
import { Upload, Camera, Link, ArrowUp } from 'lucide-react';

interface ChatInputProps {
  onSendMessage: (message: string) => void;
  onFileUpload: (files: FileList) => void;
}

const ChatInput: React.FC<ChatInputProps> = ({ onSendMessage, onFileUpload }) => {
  const [message, setMessage] = useState('');
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [isUploadOpen, setIsUploadOpen] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (message.trim()) {
      onSendMessage(message);
      setMessage('');
    }
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files.length > 0) {
      onFileUpload(e.target.files);
      setIsUploadOpen(false);
    }
  };

  const toggleUploadMenu = () => {
    setIsUploadOpen(!isUploadOpen);
  };

  return (
    <div className="relative w-full border-t border-gray-700">
      <form onSubmit={handleSubmit} className="relative flex items-center p-4">
        <div className="relative">
          <button
            type="button"
            onClick={toggleUploadMenu}
            className="p-2 text-gray-300 hover:text-white focus:outline-none focus:ring-2 focus:ring-gray-600 rounded-lg"
          >
            <Upload className="w-5 h-5" />
          </button>
          
          {isUploadOpen && (
            <div className="absolute bottom-full left-0 mb-2 w-48 bg-gray-800 rounded-lg shadow-lg border border-gray-700">
              <div className="p-2 space-y-2">
                <input
                  ref={fileInputRef}
                  type="file"
                  onChange={handleFileChange}
                  className="hidden"
                  multiple
                  accept="image/*,.pdf,.doc,.docx,.txt"
                />
                
                <button
                  type="button"
                  onClick={() => fileInputRef.current?.click()}
                  className="flex items-center w-full p-2 text-left text-sm text-gray-300 hover:bg-gray-700 rounded transition-colors duration-150"
                >
                  <Camera className="w-4 h-4 mr-2" />
                  Upload from device
                </button>
                
                <button
                  type="button"
                  onClick={() => fileInputRef.current?.click()}
                  className="flex items-center w-full p-2 text-left text-sm text-gray-300 hover:bg-gray-700 rounded transition-colors duration-150"
                >
                  <Link className="w-4 h-4 mr-2" />
                  Insert link
                </button>
              </div>
            </div>
          )}
        </div>

        <input
          type="text"
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          placeholder="Reply to Claude..."
          className="flex-1 mx-4 p-2 text-white placeholder-gray-400 bg-transparent focus:outline-none focus:ring-2 focus:ring-gray-600 rounded-lg"
        />

        <button
          type="submit"
          disabled={!message.trim()}
          className="p-2 text-white bg-blue-600 rounded-full disabled:opacity-50 disabled:cursor-not-allowed hover:bg-blue-700 transition-colors duration-150 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 focus:ring-offset-gray-900"
        >
          <ArrowUp className="w-5 h-5" />
        </button>
      </form>
    </div>
  );
};



